header{
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;

}
.header__container{
    text-align: center;
    height: 100%;
    position: relative;
}


/*=============*/

.cta{
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}
/*=============*/
.header__socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 2.3rem;
    bottom: 3rem;
}

.header__socials::after{
    content: "";
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}
/*=============*/
.me {
    background: var(--color-primary-variant);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 3rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 2rem 1.5rem 1.5rem 1.5rem;
}
/*=============*/
.scroll__down{
    position: absolute;
    right: 2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}
/*=============== MEDIA QUERIES ======*/

@media screen and (max-width:1024px){
    header{
        height: 100vh;
    }
    .header__social, .scroll__down{
        display: none;
    }
  }
  @media screen and (max-width:600px){
    .container{
      width: var(--container-width-sm);
    }
    section > h2{
      margin-bottom: 2rem;
    }
  }